/* generic theme */
/* graph */
/* highlighted */
* {
  box-sizing: border-box; }

html {
  font-size: 100%;
  height: 100%; }

body {
  height: 100%;
  padding: 0;
  margin: 0;
  color: #2b4141;
  font-family: Arial, Helvetica, sans-serif; }

.header {
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  height: 3rem;
  background-color: #3666a0;
  align-content: center;
  justify-content: space-between;
  z-index: 999; }
  .header h1 {
    color: #fff;
    margin: auto 2rem;
    height: 2rem;
    width: 30%; }
  .header__view-test {
    margin: auto 2rem;
    color: #fff; }

.class-view, .method-view {
  display: inline-block;
  height: 100%;
  overflow: scroll;
  padding: 4rem 2rem 2rem;
  position: relative; }

.class-view {
  width: 60%;
  border-right: 4px #e6e6e6 solid; }
  .class-view p {
    margin-bottom: 2rem; }
  .class-view .arrow {
    width: 0;
    height: 0;
    border-top: 20px solid transparent;
    border-bottom: 20px solid transparent;
    border-right: 20px solid #e6e6e6;
    position: absolute;
    right: 0;
    display: none; }
  .class-view .systemDiv {
    height: 70%;
    margin: auto; }
    .class-view .systemDiv .classGroup {
      cursor: pointer; }

.method-view {
  width: 39%; }
  .method-view > .mermaid {
    opacity: 0;
    position: absolute;
    margin: auto;
    width: 90%;
    transition: .2s opacity ease-in-out; }
    .method-view > .mermaid.show {
      opacity: 1; }

.cluster text {
  font-weight: 700; }

.node .label {
  color: #003863;
  transition: .7s all ease-in-out; }

.node.clickable {
  cursor: pointer; }

.node.highlighted rect, .node.highlighted circle, .node.highlighted ellipse, .node.highlighted polygon {
  fill: #ffb238;
  stroke: #f19143;
  transition: .7s all ease-in-out; }

.node.highlighted .label {
  color: #fff;
  transition: .7s all ease-in-out; }

.node rect, .node circle, .node ellipse, .node polygon {
  fill: #d9d7e6;
  stroke: #3666a0;
  stroke-width: 1px;
  transition: .7s all ease-in-out; }

.edgePath.highlighted .path {
  stroke: #f55536; }

.edgePath.highlighted .arrowheadPath {
  fill: #f55536; }

.edgePath .path {
  stroke: #3666a0;
  stroke-width: 1.5px;
  transition: .7s all ease-in-out; }

.edgePath .arrowheadPath {
  fill: #3666a0;
  transition: .7s all ease-in-out; }

.edgeLabel {
  background-color: #8ab9b5; }

.cluster rect {
  fill: #e6e6e6 !important;
  stroke: #e6e6e6 !important;
  stroke-width: 1px !important; }

.cluster text {
  fill: #003863; }

div.mermaidTooltip {
  position: absolute;
  text-align: center;
  max-width: 200px;
  padding: 2px;
  font-family: 'trebuchet ms', verdana, arial;
  font-size: 12px;
  background: #e6e6e6;
  border: 1px solid #e6e6e6;
  border-radius: 2px;
  pointer-events: none;
  z-index: 100; }

g.classGroup.highlighted rect {
  fill: #ffb238;
  stroke: #f19143;
  transition: .7s all ease-in-out; }

g.classGroup.highlighted text {
  color: #fff;
  fill: #fff;
  transition: .7s all ease-in-out; }

g.classGroup.highlighted line {
  stroke: #f19143;
  transition: .7s all ease-in-out; }

g.classGroup > text:first-of-type {
  font-weight: 700;
  color: black;
  fill: black; }

g.classGroup text {
  color: #003863;
  fill: #003863;
  stroke: none;
  font-size: 10px; }

g.classGroup rect {
  fill: #d9d7e6;
  stroke: #3666a0; }

g.classGroup line {
  stroke: #3666a0;
  stroke-width: 1; }

.classLabel .box {
  stroke: none;
  stroke-width: 0;
  fill: #d9d7e6;
  opacity: 0.5; }

.classLabel .label {
  fill: #3666a0;
  font-size: 10px; }

.relation {
  stroke: grey;
  stroke-width: 1;
  fill: none; }

#compositionStart,
#compositionEnd,
#dependencyStart,
#dependencyEnd,
#extensionStart,
#extensionEnd {
  fill: grey;
  stroke: grey;
  stroke-width: 1; }
  #compositionStart.hightlighted,
  #compositionEnd.hightlighted,
  #dependencyStart.hightlighted,
  #dependencyEnd.hightlighted,
  #extensionStart.hightlighted,
  #extensionEnd.hightlighted {
    fill: #f55536;
    stroke: #f55536; }

#aggregationStart,
#aggregationEnd {
  fill: grey;
  stroke: grey;
  stroke-width: 1; }
  .hightlighted #aggregationStart, .hightlighted
  #aggregationEnd {
    fill: #f55536;
    stroke: #f55536; }
